import { Button } from "@webs/component-library/button";

import { addCodeExample } from "./helpers/code-example";

export default {
  title: "Example/Button",
  argTypes: {
    hoverColor: {
      control: {
        type: "select",
        options: ["defaut", "violet", "pink"],
      },
    },
    text: {
      control: {
        type: "select",
        options: ["Short", "Medium Length", "Rather Long Button Text"],
      },
    },
    onclick: "",
  },
};

const Template = (args) => {
  return `
    <webs-button
      text=${args.text}
      extraClasses="${args.hoverColor}"
      href=""
      openInNewTab="true"
    ></webs-button>
  `;
};

export const Primary = Template.bind({
  hoverColor: "default",
  text: "Short",
});

